<template>
    <div class="order" v-loading="loading">
        <div class="order-head">
            <el-form :inline="true" :model="search" class="form-inline" size="small">
                <el-form-item class="form-item"
                              v-if="$route.params.hasOwnProperty('type') && $route.params.type === 'afterSale'">
                    <el-select v-model="search.afterSaleStatus" class="form-select" @change="doSearch">
                        <el-option :label="item.label" :value="item.value" :key="item.value"
                                   v-for="item in orderTypeList"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="form-item"
                              v-if="$route.params.hasOwnProperty('type') && $route.params.type !== 'needPay' && $route.params.type !== 'needReceipt'">
                    <el-select v-model="search.timeScreening" class="form-select" @change="doSearch">
                        <el-option :label="item.label" :value="item.value" :key="item.value"
                                   v-for="item in orderTimeList"></el-option>
                    </el-select>
                </el-form-item>
                <el-form-item class="form-item">
                    <el-input v-model="search.keyword" :placeholder="$L('搜索：产品名称/订单号')" class="form-input"
                              @change="doSearch">
                        <i slot="suffix" class="el-input__icon el-icon-search"></i>
                    </el-input>
                </el-form-item>
            </el-form>
        </div>
        <order-list :order-list="orderList && orderList.hasOwnProperty('list') ? orderList.list : []" @reloadData="reloadData"></order-list>
        <el-pagination
                v-if="orderList && orderList.hasOwnProperty('list') && orderList.list.length>0"
                class="list-pages"
                :background="true"
                @size-change="sizeChange"
                @current-change="currentChange"
                :current-page="nowPage"
                :page-size="pageNum"
                layout="prev, pager, next"
                :total="orderList.allNum">
        </el-pagination>
    </div>
</template>

<script>
    import orderList from "../../../components/order/orderList.vue";

    export default {
        name: "order",
        components: {
            orderList
        },
        data() {
            let pageNum = localStorage.getItem('uc_page_size');
            if (pageNum) {
                pageNum = parseInt(pageNum);
                if (isNaN(pageNum)) {
                    pageNum = 20;
                }
            } else {
                pageNum = 20;
            }
            return {
                loading: true,
                orderTypeList: [
                    {
                        value: 0,
                        label: this.$L("全部状态")
                    },
                    {
                        value: 1,
                        label: this.$L("退款中")
                    },
                    {
                        value: 2,
                        label: this.$L("退款成功")
                    },
                    {
                        value: 3,
                        label: this.$L("退款失败")
                    },
                ],
                orderTimeList: [
                    {
                        value: "all",
                        label: this.$L("全部时间")
                    },
                    {
                        value: "seven",
                        label: this.$L("近7天订单")
                    },
                    {
                        value: "thirty",
                        label: this.$L("近30天订单")
                    },
                    {
                        value: "halfYear",
                        label: this.$L("近半年订单")
                    },
                    {
                        value: "year",
                        label: this.$L("近1年订单")
                    },
                    {
                        value: "yearAgo",
                        label: this.$L("1年以前订单")
                    }
                ],
                pageNum: pageNum,
                nowPage: 1,
                search: {
                    afterSaleStatus: 0,
                    timeScreening: "all",
                    keyword: ''
                },
                orderList: {},
            }
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.nowPage = 1;
            this.search = {
                afterSaleStatus: 0,
                timeScreening: "all",
                keyword: ''
            };
            this.getData(to.params.hasOwnProperty('type') ? to.params.type : 'all');
            next();
        },
        created() {
            this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
        },
        methods: {
            reloadData(){
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            sizeChange(num) {
                this.pageNum = num;
                localStorage.setItem('uc_page_size', num);
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            currentChange(num) {
                this.nowPage = num;
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            doSearch() {
                this.getData(this.$route.params.hasOwnProperty('type') ? this.$route.params.type : 'all');
            },
            getData(type) {
                const _self = this;
                _self.loading = true;
                let postData = {
                    type: type,
                    keyword: _self.search.keyword,
                    p: _self.nowPage,
                    num: _self.pageNum,
                    afterSaleStatus: _self.search.afterSaleStatus,
                    timeScreening: _self.search.timeScreening,
                };
                _self.$api('getOrderList', postData, function (reData) {
                    if (reData.flag === 1) {
                        _self.orderList = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$gotoWebHome();
                    });
                });
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/common.scss";

    .order {
        position: relative;
        width: 100%;

        .order-head {
            position: relative;
            width: 100%;
            height: 43px;
            z-index: 100;
            text-align: right;

            .form-inline {
                height: 33px;

                .form-item {
                    margin-right: 0;
                    margin-left: 10px;
                }

                .form-select {
                    width: 120px;
                }

                .form-input {
                    width: 220px;
                }
            }
        }

        .list-pages {
            text-align: center;
            padding: 10px 0 20px;

            /deep/ .el-pager .number {
                background: #fff;
            }
        }
        .error {
            position: absolute;
            z-index: 200;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: #FFFFFF;
            overflow: hidden;

            .error-tips {
                position: absolute;
                top: 50%;
                left: 50%;
                width: 320px;
                transform: translate(-50%, -50%);
            }
        }
    }
</style>